e and\n        design a solution for it. I was initially tasked with addressing the\n        problem of how one might better equip people with tools for financial\n        planning by leveraging my design experience. Many young adults do not\n        practice any financial planning and lack basic financial literacy.\n        Overall, majority of young adults live in a reality where they have\n        little knowledge of how to start saving up for long term financial\n        goals, investing their income, or budgeting their expenses.\n      </h4>")],2)]),_vm._ssrNode(" "),_vm._ssrNode("<div data-aos=\"fade-up\" data-v-fc48a73c>","</div>",[_vm._ssrNode("<div class=\"coordina-ideation\" data-v-fc48a73c>","</div>",[_c('h7',[_vm._v("Design Process")]),_vm._ssrNode(" <div class=\"td-body-text\" data-v-fc48a73c><h5 data-v-fc48a73c><b data-v-fc48a73c>1. Define the problem space</b></h5> <div class=\"td-body-text-indent\" data-v-fc48a73c><h6 data-v-fc48a73c>‣ Brainstormed different concepts for our final solution</h6></div> <h5 data-v-fc48a73c><b data-v-fc48a73c>2. Research</b></h5> <div class=\"td-body-text-indent\" data-v-fc48a73c><h6 data-v-fc48a73c>\n            ‣ Utilized user surveys to develop empathy for the users that we\n            were designing for which gave us further insight into the needs,\n            motivations, and goals of a potential user\n          </h6> <h6 data-v-fc48a73c>\n            ‣ Conducted high level market research through secondary research\n          </h6> <h6 data-v-fc48a73c>‣ Conducted competitive analysis</h6></div> <h5 data-v-fc48a73c><b data-v-fc48a73c>3. Analysis</b></h5> <div class=\"td-body-text-indent\" data-v-fc48a73c><h6 data-v-fc48a73c>‣ Drew insights from the data collected</h6> <h6 data-v-fc48a73c>‣ Formed a user persona and user journey map</h6></div> <h5 data-v-fc48a73c><b data-v-fc48a73c>4. Design</b></h5> <div class=\"td-body-text-indent\" data-v-fc48a73c><h6 data-v-fc48a73c>\n            ‣ Referred to our analysis of the user and created a final design\n          </h6> <h6 data-v-fc48a73c>‣ Completed two iterations of our design</h6></div></div>")],2)]),_vm._ssrNode(" "),_vm._ssrNode("<div data-aos=\"fade-up\" data-v-fc48a73c>","</div>",[_vm._ssrNode("<div class=\"upbudget-body\" data-v-fc48a73c>","</div>",[_c('h7',[_vm._v("Solution")]),_vm._ssrNode(" <div class=\"td-body-text-indent\" data-v-fc48a73c><h6 data-v-fc48a73c>\n          The solution to the initial prompt came down to a mobile financial\n          app designed to assist users with the planning of their financial\n          future while leveraging a user’s monthly savings into\n          micro-investments to boost returns.\n        </h6></div>")],2)]),_vm._ssrNode(" <div data-aos=\"fade-up\" data-v-fc48a73c><div class=\"upbudget-prototype\" data-v-fc48a73c><a data-v-fc48a73c><iframe width=\"800\" height=\"450\" src=\"https://www.figma.com/embed?embed_host=share&url=https%3A%2F%2Fwww.figma.com%2Fproto%2FqaLTOHTSbOg9x6gGlkFIas%2Fupbudget%3Fpage-id%3D0%253A1%26node-id%3D1%253A1287%26viewport%3D1590%252C301%252C0.5220688581466675%26scaling%3Dcontain\" allowfullscreen=\"allowfullscreen\" style=\"border: 1px solid rgba(0, 0, 0, 0.1)\" data-v-fc48a73c></iframe></a></div></div> "),_vm._ssrNode("<div data-aos=\"fade-up\" data-v-fc48a73c>","</div>",[_vm._ssrNode("<div class=\"coordina-ideation\" data-v-fc48a73c>","</div>",[_c('h7',[_vm._v("Next steps")]),_vm._ssrNode(" <div class=\"td-body-text\" data-v-fc48a73c><h5 data-v-fc48a73c><b data-v-fc48a73c> Research and testing</b></h5> <div class=\"td-body-text-indent\" data-v-fc48a73c><h6 data-v-fc48a73c>\n            ‣ conduct expanded user research in the form of user interviews\n            where I would have the opportunity to improve the scope of the\n            problem space and further enhance my understanding of the users\n            that we were designing for\n          </h6> <h6 data-v-fc48a73c>\n            ‣ conduct usability testing to validate if the solution solved 